@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.9.1/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:wght@700&family=Signika&display=swap);
.btn-saya {
  margin-top: 20px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  transition: 1s;
}
.btn-saya:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.card {
  transition: 0.5s;
}

.card:hover {
  -webkit-transform: rotateX(30deg);
          transform: rotateX(30deg);
}

.merah {
  border-left: red solid 10px;
}
.hijau {
  border-left: green solid 10px;
}
.kuning {
  border-left: yellow solid 10px;
}
.biru {
  border-left: cyan solid 10px;
}

body {
font-family: 'PT Serif', serif;
font-family: 'Signika', sans-serif;
}

.intro {
  background: url("https://images.unsplash.com/photo-1663684984083-ba47b6e3cbb6?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80");
}


.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar {
  width: 200px;
  border-radius: 100px;
}

.social-media {
  font-size: 30px;
  color: white;
  margin-left: 10px;
  margin-right: 10px;
  transition: 1s;
}

.gambarku {
  transition: 0.9s;
}
.gambarku:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

